<template>
  <el-row class="vip-video">
    <el-row class="video-search">
      <el-row class="mt-20">
        <el-input v-model="searchName" size="medium" placeholder="请输入你想看的影片" style="width: 300px;"></el-input>
        <el-row class="text-center mt-20">
          <el-button @click="getData" type="primary">尽力搜索</el-button>
          <el-button @click="toPaste" type="primary">我有播放地址</el-button>
        </el-row>
      </el-row>
      <el-row class="mt-20" type="flex" justify="space-between" v-loading="loading">
        <el-row v-for="item in videoList" :key="item.id" @click.native="handleVideo(item)" style="cursor: pointer;" class="m-20">
          <el-image :src="item.dc" style="width: 100px;height: 100px;">
            <div slot="error" class="image-error">
              <img src="img/image-error.png" alt="暂无封面" style="width: 100%;height: 100%;">
            </div>
          </el-image>
          <el-row>{{item.word}}</el-row>
        </el-row>
      </el-row>
    </el-row>
    <el-row class="mt-20">
      <iframe id="$iframe" src="" frameborder="0" width="100%" :height="selectVideo?'800px':'0'" allowfullscreen="true"
              webkitallowfullscreen="true"
              mozallowfullscreen="true"></iframe>
      <el-row style="color: #909299;">无法播放？切换线路试试</el-row>
      <el-radio-group v-model="selectLine" @change="changeLine">
        <el-radio border class="m-10" v-for="item in apiList" :key="item.id" :label="item.value">{{item.label}}
        </el-radio>
      </el-radio-group>
    </el-row>
    <!--<el-backtop target=".vip-video" :bottom="100">-->
    <!--<div-->
    <!--style="{-->
    <!--height: 100%;-->
    <!--width: 100%;-->
    <!--background-color: #f2f5f6;-->
    <!--box-shadow: 0 0 6px rgba(0,0,0, .12);-->
    <!--text-align: center;-->
    <!--line-height: 40px;-->
    <!--color: #1989fa;-->
    <!--}"-->
    <!--&gt;-->
    <!--UP-->
    <!--</div>-->
    <!--</el-backtop>-->
    <el-row class="navButton">
      <el-button @click="goTop" title="返回顶部">top</el-button>
      <!--<el-button @click="goTop"><i class="el-icon-position"></i></el-button>-->
    </el-row>
  </el-row>
</template>
<script>

export default {
  name: 'vip-video',
  data () {
    return {
      searchName: '',
      loading: false,
      videoList: [],
      selectLine: '',
      selectVideo: '',
      apiList: [],
    }
  },
  created () {
    this.getApiList()
  },
  methods: {
    async getApiList () {
      this.apiList = await this.$store.dispatch('api/videoApiListGet')
      console.log('this.apiList', this.apiList)
      this.selectLine = this.apiList[0] && this.apiList[0].value
    },
    getData () {
      if (!this.searchName) {
        return this.$message.warning('请输入要搜索的资源')
      }
      this.loading = true
      this.$jsonp(`https://s.video.qq.com/smartbox?callback=result&plat=2&ver=0&num=20&otype=json&query=${this.searchName}`, {
        ts: new Date().getTime(),
        callbackName: 'callback'
      }).then(res => {
        if (res.result.code === 0) {
          // this.$message.success('查询成功')
          this.videoList = res.item.filter(v => v.class && v.url)
        } else {
          this.$message.error('检索失败，未找到相应视频源')
        }
        // Success.
        console.log('结果', res)
        this.loading = false
      }).catch(err => {
        this.loading = false
        console.log('err', err)
      })
      // this.createScript(this.searchName)
    },
    toPaste () {
      this.$router.push({
        name: 'play'
      })
    },
    handleVideo (val) {
      let $iframe = document.getElementById('$iframe')
      $iframe.src = this.selectLine + val.url
      this.selectVideo = val.url
    },
    changeLine () {
      if (this.selectVideo) {
        let $iframe = document.getElementById('$iframe')
        $iframe.src = this.selectLine + this.selectVideo
        console.log('$iframe', $iframe)
      } else {
        this.$message.info('暂未选择播放源')
      }

    },
    goTop () {
      window.scrollTo(0, 0)
    }
  }
}
</script>
<style lang="scss">
.vip-video {
  width: 100%;

  .video-search {
    display: flex;
    height: calc(100vh - 162px);
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-image: linear-gradient(to top, #fbc2eb 0%, #a6c1ee 100%);
  }

  .image-error {
    width: 100%;
    height: 100%;
    background: #000;
    opacity: .5;
  }

  .navButton {
    position: fixed;
    right: 10px;
    bottom: 80px;
  }
}
</style>
